* {
  box-sizing: border-box;
  
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:darkgrey ;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

h1 {
  color: antiquewhite;
  text-align: center;
}

input {
  align-items: center;
  display: flex;
  margin: auto;
  border-radius: 5px;
  border-color: bisque;
  text-align: center;
}